import { HTMLAttributes, useMemo } from 'preact/compat';
import { FunctionComponent, JSX } from 'preact';
import { classes } from '@adobe-commerce/elsie/lib';
import {
  Button,
  Icon,
  ImageNodeRenderProps,
  Price,
} from '@adobe-commerce/elsie/components';
import { ImageCarousel } from '@/wishlist/components';
import { Item, Product } from '@/wishlist/data/models';
import { Cart, Trash } from '@adobe-commerce/elsie/icons';
import { useText } from '@adobe-commerce/elsie/i18n';

import '@/wishlist/components/ProductItem/ProductItem.css';

export interface ProductItemProps extends HTMLAttributes<HTMLDivElement> {
  className?: string;
  item?: Item;
  onCartActionButtonClick?: () => boolean;
  onTrashButtonClick?: () => boolean;
  routeProdDetailPage: (product: Product) => string;
  imageNode?: (props: {
    defaultImageProps: ImageNodeRenderProps;
  }) => JSX.Element;
}

export const ProductItem: FunctionComponent<ProductItemProps> = ({
  className,
  item,
  onCartActionButtonClick,
  onTrashButtonClick,
  routeProdDetailPage,
  imageNode,
  ...props
}) => {
  const labels = useText({
    cartActionBtn: 'ProductItem.CartActionButton',
    trashActionBtn: 'ProductItem.TrashActionButton',
    customizeActionBtn: 'ProductItem.CustomizeActionButton',
  });

  const discounted =
    item.product?.prices?.final?.amount < item.product?.prices?.regular?.amount;

  const renderImage = () => {
    return (
      <a
        className="wishlist-product-item-image"
        data-testid="wishlist-product-item-image"
        href={routeProdDetailPage(item.product)}
      >
        <ImageCarousel
          images={item.product?.images ?? []}
          imageNode={imageNode}
        />
      </a>
    );
  };

  const renderPrices = () => {
    if (
      item.product?.prices?.final?.amount === undefined &&
      item.product?.prices?.final?.maximumAmount !== undefined &&
      item.product?.prices?.final?.maximumAmount > 0
    ) {
      return (
        <>
          <Price
            className="wishlist-product-item-price"
            data-testid="wishlist-product-item-price"
            amount={item.product?.prices?.final?.minimumAmount}
            currency={item.product?.prices?.final?.currency}
          />
          -
          <Price
            className="wishlist-product-item-price"
            data-testid="wishlist-product-item-price"
            amount={item.product?.prices?.final?.maximumAmount}
            currency={item.product?.prices?.final?.currency}
          />
        </>
      );
    }
    return (
      <>
        <Price
          className={classes([
            'wishlist-product-item-price',
            discounted ? 'strikethrough' : '',
          ])}
          data-testid="wishlist-product-item-price"
          amount={item.product?.prices?.regular?.amount}
          currency={item.product?.prices?.regular?.currency}
        />

        {discounted && (
          <Price
            className="wishlist-product-item-discounted-price"
            data-testid="wishlist-product-item-discounted-price"
            amount={item.product?.prices?.final?.amount}
            currency={item.product?.prices?.final?.currency}
          />
        )}
      </>
    );
  };

  const renderTrashButton = () => {
    return (
      <Button
        data-testid="wishlist-product-item-remove-button"
        className="wishlist-product-item-button__remove"
        variant="tertiary"
        onClick={() => onTrashButtonClick?.()}
        icon={
          <Icon
            source={Trash}
            size="24"
            stroke="2"
            viewBox="0 0 24 24"
            aria-label={labels.trashActionBtn}
          />
        }
      />
    );
  };

  const renderMainActionButton = () => {
    return isProductReady()
      ? renderMoveToCartButton()
      : renderCustomizeButton();
  };

  const isProductReady = () => {
    if (item.product?.productType === 'complex') {
      return areAllRequiredOptionsIncluded();
    }
    return true;
  };

  const selectedOptions = useMemo(
    () => item.selectedOptions?.map((opt: any) => opt.uid) || [],
    [item.selectedOptions]
  );

  const requiredOptions = useMemo(() => {
    const options = item.product.options || [];
    const required = options.filter((option: any) => option.required);
    return required.length === 0 ? options : required;
  }, [item.product.options]);

  const areAllRequiredOptionsIncluded = (): boolean => {
    return requiredOptions.every((option: any) =>
      option.items?.some((item: any) => selectedOptions.includes(item.id))
    );
  };

  const renderOptions = () => {
    if (selectedOptions.length === 0) return null;

    return (
      <div className="wishlist-product-item-options">
        {requiredOptions.map((option: any) => {
          const selectedValue = option.items?.find((item: any) =>
            selectedOptions.includes(item.id)
          );
          return selectedValue ? (
            <div key={option.type} className="wishlist-product-item-option">
              <span className="wishlist-product-item-option__attribute">
                {option.id}:
              </span>
              &nbsp;
              <span className="wishlist-product-item-option__label">
                {selectedValue.label}
              </span>
            </div>
          ) : null;
        })}
      </div>
    );
  };

  const renderMoveToCartButton = () => {
    return (
      <Button
        data-testid="wishlist-product-item-move-to-cart-button"
        size="medium"
        type="submit"
        icon={<Icon source={Cart} />}
        disabled={!item.product?.inStock}
        aria-label={labels.cartActionBtn}
        onClick={() => onCartActionButtonClick?.()}
      >
        {labels.cartActionBtn}
      </Button>
    );
  };

  const renderCustomizeButton = () => {
    return (
      <Button
        data-testid="wishlist-product-item-customize-button"
        size="medium"
        type="submit"
        aria-label={labels.customizeActionBtn}
        href={routeProdDetailPage(item.product)}
      >
        {labels.customizeActionBtn}
      </Button>
    );
  };

  return (
    <div {...props} className={classes(['wishlist-product-item', className])}>
      <div className="wishlist-product-item__content">
        {renderImage()}
        <div
          className="wishlist-product-item__title"
          data-testid="wishlist-product-item-header"
        >
          <a
            className="wishlist-product-item-name"
            data-testid="wishlist-product-item-name"
            href={routeProdDetailPage(item.product)}
          >
            {item.product?.name}
          </a>

          {renderTrashButton()}
          {renderPrices()}
        </div>
        {renderOptions()}
        {renderMainActionButton()}
      </div>
    </div>
  );
};
