import {
  HTMLAttributes,
  useEffect,
  useState,
  useCallback,
} from 'preact/compat';
import { Container } from '@adobe-commerce/elsie/lib';
import { ProductItem } from '@/wishlist/components';
import { Item, Product } from '@/wishlist/data/models';
import { removeProductsFromWishlist } from '@/wishlist/api';
import { events } from '@adobe-commerce/event-bus';
import { ImageNodeRenderProps } from '@adobe-commerce/elsie/components';
import { JSX } from 'preact';

export interface WishlistItemProps extends HTMLAttributes<HTMLDivElement> {
  item: Item;
  getProductData?: (sku: string) => Promise<Product | null>;
  getRefinedProduct?: (
    sku: string,
    optionUIDs: string[],
    anchorOptions?: string[],
    raw?: boolean
  ) => Promise<Product | null>;
  moveProdToCart: (
    products: {
      sku: string;
      quantity: number;
      optionsUIDs?: [];
      enteredOptions?: [];
    }[]
  ) => Promise<any>;
  routeProdDetailPage: (product: Product) => string;
  imageNode?: (props: {
    defaultImageProps: ImageNodeRenderProps;
  }) => JSX.Element;
}

export const WishlistItem: Container<WishlistItemProps> = ({
  item,
  getProductData,
  getRefinedProduct,
  moveProdToCart,
  routeProdDetailPage,
  imageNode,
}: WishlistItemProps) => {
  const [productData, setProductData] = useState<Product | null>(null);

  useEffect(() => {
    let cancelled = false;

    async function fetchProductData() {
      try {
        let data: Product | null;
        if (getRefinedProduct && item?.selectedOptions?.length > 0) {
          data = await getRefinedProduct(
            item.product.sku,
            item.selectedOptions.map((option: { uid: any }) => option.uid)
          );
        } else {
          data = getProductData
            ? await getProductData(item?.product.sku)
            : item?.product;
        }
        if (!cancelled) setProductData(data);
      } catch (err) {
        if (!cancelled) {
          console.error('Failed to fetch product data:', err);
        }
      }
    }

    fetchProductData();

    return () => {
      cancelled = true;
    };
  }, [item, getProductData, getRefinedProduct]);

  /**
   * Removes product from wishlist
   */
  const removeProductFromWishlist = useCallback(
    async (showAlert: boolean = true): Promise<boolean> => {
      try {
        await removeProductsFromWishlist([item]);
        console.log(`Product ${productData?.sku} removed from wishlist!`);
        if (showAlert) {
          events.emit('wishlist/alert', {
            action: 'remove',
            item: { ...item, product: productData },
          });
        }
        return true;
      } catch (error) {
        console.error(
          `Product ${productData.sku} could not be removed from wishlist`,
          error
        );
        return false;
      }
    },
    [item, productData]
  );

  /**
   * Moves product to cart
   */
  const moveProductToCart = useCallback(async (): Promise<boolean> => {
    try {
      await moveProdToCart([
        {
          sku: productData.sku,
          quantity: 1,
          optionsUIDs: item.selectedOptions?.map(
            (option: { uid: any }) => option.uid
          ),
          enteredOptions: item.enteredOptions,
        },
      ]);
      console.log(`Product ${productData.sku} successfully moved to cart ðŸ›’`);
      events.emit('wishlist/alert', {
        action: 'move',
        item: { ...item, product: productData },
      });
      return await removeProductFromWishlist(false);
    } catch (error) {
      console.error('Could not move product to cart: ', error);
      if (
        error.toString().includes('You need to choose options for your item.')
      ) {
        window.location.replace(routeProdDetailPage(productData));
      }
      return false;
    }
  }, [
    productData,
    item,
    moveProdToCart,
    routeProdDetailPage,
    removeProductFromWishlist,
  ]);

  if (!productData || !item) return null;

  return (
    <ProductItem
      item={{ ...item, product: productData }}
      onCartActionButtonClick={moveProductToCart}
      onTrashButtonClick={removeProductFromWishlist}
      routeProdDetailPage={routeProdDetailPage}
      imageNode={imageNode}
    />
  );
};
