import { FunctionComponent, JSX } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe-commerce/elsie/lib';
import { Image, ImageNodeRenderProps } from '@adobe-commerce/elsie/components';
import { useState } from 'react';

import '@/wishlist/components/ImageCarousel/ImageCarousel.css';

export interface ImageCarouselProps extends HTMLAttributes<HTMLDivElement> {
  className?: string;
  children?: any;
  images: Image[];
  imageNode?: (props: {
    defaultImageProps: ImageNodeRenderProps;
  }) => JSX.Element;
}

export const ImageCarousel: FunctionComponent<ImageCarouselProps> = ({
  className,
  children,
  images,
  imageNode,
  ...props
}) => {
  const [carouselIndex, setCarouselIndex] = useState(0);

  return (
    <>
      <div {...props} className={classes(['image-carousel', className])}>
        <div
          className={classes([
            'overflow-hidden relative max-w-[200px]',
            className,
          ])}
        >
          {images?.map(
            (image: ImageCarouselProps['images'][0], index: number) => {
              const defaultProps = {
                className: classes(['image-carousel-image', className]),
                src: image.url,
                alt: image.label,
              } as const;

              if (index !== carouselIndex) {
                return null;
              }

              return typeof imageNode === 'function'
                ? imageNode({
                    defaultImageProps: defaultProps,
                  })
                : imageNode || <Image {...defaultProps} />;
            }
          )}
        </div>
      </div>
      {images?.length > 1 && (
        <div className={classes(['absolute', 'image-switcher-area'])}>
          {images?.map(
            (_image: ImageCarouselProps['images'][0], index: number) => {
              return (
                <span
                  className={classes([
                    'image-switcher',
                    carouselIndex === index
                      ? 'image-switcher-active'
                      : 'image-switcher-inactive',
                  ])}
                  key={index}
                  onClick={(event: MouseEvent | TouchEvent) => {
                    setCarouselIndex(index);
                    event.preventDefault(); // prevent navigate on click since ImageCarousel is wrapped in a link
                    event.stopPropagation();
                  }}
                />
              );
            }
          )}
        </div>
      )}
    </>
  );
};
